.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.video-container{
  width: 400px;
  height:400px;
  background: red;
  margin:10px auto;
  position: relative;
}

video{
  left:0;
  position: absolute;
  z-index: 1;
}

canvas{
  left:0;
  position: absolute;
  z-index: 2;
}
